<template>
  <div id="code-box">
    <div class="qing">请扫码付款</div>
    <div>
      <div class="price_s">¥{{ Number($route.query.roomPrice).toFixed(2) }}</div>
      <img
        style="margin-bottom:30px;"
        :src="pic_code"
      />
    </div>
    <div class="flex">
      <img
        style="width:40%;"
        v-for="(item,i) in top_pic"
        :key="i"
        :src="item.pic"
        alt
      />
    </div>
  </div>
</template>
<script>
import { GetBusinessAccountUnitQrPay } from '@/api/api_business_hotel_and_scale'
export default {
  data() {
    return {
      currentInfo: null,
      pic_code: null,
      top_pic: [
        { pic: require("@/assets/two/weix.png") },
        { pic: require("@/assets/two/zhifb.png") }
      ],
    };
  },
  created() {
    this.$store.commit('setHeader', '付款码');
    this.getCode()
  },
  methods: {
    getCode() {
      GetBusinessAccountUnitQrPay({ billSn: this.$route.query.billSn }).done(res => {
        this.pic_code = "data:image/png;base64," + res.data;
      })
    }
  }
};
</script>
<style scoped>
#code-box {
    font-size: 14px;
    color: #000;
    margin: 20px;
    background: #fff;
    border-radius: 0 0 10px 10px;
}
.qing {
    background: #ff7d41;
    color: #fff;
    padding: 10px;
    border-radius: 10px 10px 0 0;
    font-size: 18px;
}
.price_s {
    font-weight: bold;
    font-size: 22px;
    padding-top: 20px;
}
.flex {
    display: flex;
    justify-content: space-around;
    margin: 0 30px;
}
</style>



